.popular-movie {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-column-gap: 1rem;
  grid-row-gap: 2rem;
}

img {
  width: 100%;
  height: 30vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom: 1rem;
}

h2 {
  font-size: 0.8rem;
}

.filter {
  display: flex;
  flex-direction: column;
}
.filter-container {
  margin-bottom: 1rem;
}
.filter-container button {
  margin-right: 2rem;
  min-width: 5rem;
  padding: 0.5rem 1rem;
  border: 2px solid #171a31;
  border-radius: 1rem;
  background: #fff;
  color: #3b255e;
  font-weight: bold;
  cursor: pointer;
}

button.active {
  background: rgba(121, 42, 185, 0.333);
  color: #fff;
}
